body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.button{
  background-color: #D9D9D9; /* Green */
  border: none;
  color: #020D13;
  padding: 20px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 20px;
}

.App-header {
  background-color: #020D13;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(30px + 2vmin);
  color: white;
  
}

.App-body {
  margin-top: 0;
  font-size: calc(13px + 2vmin);
  text-align: left;
  margin: 0px;
}

.App-link {
  color: #61dafb;
}


.FilmsWatched {
  text-align: center;
}

.h1{
  font-size: calc(100px + 2vmin);
}

.FilmsWatched-Header {
  background-color: #CE62B6;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(30px + 2vmin);
  color: white;
  
}

.FilmsWatchedBody {
  margin-top: 0;
  font-size: calc(13px + 2vmin);
  text-align: left;
  margin: 0px;
}

.App-link {
  color: #61dafb;
}


