.App {
  text-align: center;
}

.button{
  background-color: #D9D9D9; /* Green */
  border: none;
  color: #020D13;
  padding: 20px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 20px;
}

.App-header {
  background-color: #020D13;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(30px + 2vmin);
  color: white;
  
}

.App-body {
  margin-top: 0;
  font-size: calc(13px + 2vmin);
  text-align: left;
  margin: 0px;
}

.App-link {
  color: #61dafb;
}

